import React from 'react';
import { Link } from 'react-router-dom';
import '../App.css';


const Story = (props) => {
    const  story  = props.item;

    return(
        <div className="card-container">
            
            <div className="desc">
                <h2>
                   
                   <Link to={`/content/${story.story}`}>   { story.story }</Link>
                   
                </h2>

            </div>
        </div>
    )
};

export default Story;